body {
  flex-direction: column;
  align-items: center;
  display: flex;
}

header {
  z-index: 100;
  background-color: #000;
  justify-content: center;
  width: 100%;
  height: 64px;
  display: flex;
  position: sticky;
  top: 0;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 8px;
  display: flex;
}

.header-content h1 {
  color: #fff;
  margin: 0;
}

.header-nav-list {
  justify-content: space-between;
  padding: 0;
  list-style: none;
  display: flex;
}

.header-nav-item a {
  color: #fff;
  margin: 0 10px;
  font-size: large;
  text-decoration: none;
}

.header-nav-item a:hover {
  color: #8fbc8f;
}

.container {
  flex-direction: row;
  align-items: stretch;
  width: min(100%, 1200px);
  height: 100%;
  margin: 0 auto;
  display: flex;
}

aside {
  background-image: linear-gradient(#acd4cccc 0%, #75abafcc 100%);
  width: 320px;
  min-height: 100vh;
}

.profile {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 16px;
  padding: 16px;
  display: flex;
  position: sticky;
  top: 80px;
}

.profile img {
  border-radius: 50%;
  width: 100px;
  height: 100px;
  margin: 16px;
}

.account-list {
  flex-direction: row;
  align-items: center;
  margin: 16px 0;
  list-style: none;
  display: flex;
}

.account-item a img {
  width: 48px;
  padding: 8px;
}

.location, .university {
  justify-content: center;
  align-items: center;
  display: flex;
}

main {
  flex-direction: column;
  justify-content: center;
  width: 100%;
  display: flex;
}

section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: -64px;
  padding: 64px 16px 16px;
  display: flex;
}

.section-title {
  text-align: center;
  white-space: nowrap;
  width: 100%;
  padding: 0 32px;
  font-size: xx-large;
  position: relative;
}

.section-title span {
  z-index: 1;
  background-color: #e8fcfbe6;
  border-radius: 8px;
  padding: 0 16px;
  position: relative;
}

.section-title:before {
  content: "";
  background-color: #a3a3a3;
  width: max(50%, 240px);
  height: 12px;
  position: absolute;
  top: calc(50% - 6px);
  left: calc(50% + max(50%, 240px) / -2);
  transform: skew(-30deg);
}

.section-title:after {
  content: "";
  background: radial-gradient(circle, #00ff9d 0%, #4bccc1 100%);
  width: max(40%, 200px);
  height: 12px;
  position: absolute;
  top: calc(50% - 6px);
  left: calc(50% + max(40%, 200px) / -2);
  transform: skew(-30deg);
}

.activity-list {
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 16px;
  width: 100%;
  margin: 16px 0;
  display: grid;
}

.activity-item {
  background-color: #c5ecea;
  border-radius: 32px;
  flex-direction: column;
  width: 100%;
  min-width: 240px;
  margin: 8px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 4px 2px #0000001a;
}

.activity-item img {
  object-fit: cover;
  height: 160px;
}

.activity-item h3 {
  text-align: center;
}

.activity-item-abstract {
  width: 100%;
  padding: 16px;
}

.activity-item-abstract h4 {
  border-bottom: 3px double #000;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0 20% 0 10%;
}

.activity-item-abstract p {
  text-indent: 1em;
  text-align: left;
}

.language-list, .tool-list {
  flex-flow: wrap;
  align-items: center;
  margin: 16px 0;
  list-style: none;
  display: flex;
  position: relative;
}

.language-item, .tool-item {
  background-color: #fff;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 20%;
  min-width: 128px;
  margin: 8px;
  padding: 8px;
  display: flex;
}

.language-item img, .tool-item img {
  width: 48px;
  height: 48px;
}

.education-content, .qualification-content {
  align-items: left;
  flex-direction: column;
  width: clamp(256px, 70%, 600px);
  margin: 16px 0;
  display: flex;
  position: relative;
}

.education-content:before, .qualification-content:before {
  content: "";
  z-index: -1;
  background: linear-gradient(-120deg, #48d8cc 0%, #95e9d7 100%);
  width: calc(100% + 48px);
  height: 100%;
  position: absolute;
  top: .5em;
  left: -24px;
}

.education-content:before {
  animation: 60s ease-in-out infinite wavy-box-animation1;
}

.qualification-content:before {
  animation: 60s ease-in-out infinite wavy-box-animation2;
}

.education-content h3, .qualification-content h3 {
  border-bottom: 2px solid #000;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: 10%;
